#formsign{
    width: 100%;
}
#formsign .register{
    height: 80vh;
    width: fit-content;
    margin-top: 5.4rem;
    border: 2px solid white;
    border-radius: 0.2rem;
    font-size: 1.1rem;
    margin-left: 27rem;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right:5rem;
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem black;
}
@media screen and (max-width:600px){
    #formsign .register{
        height: 21rem;
        width: fit-content;
        font-size: 0.7rem;
        margin-left: 0.2rem;
        padding-left: 1.1rem;
        padding-right: 0.9rem;
    }
}
.register .sddd{
    font-size: 2rem;
    text-align: center;
    margin-top: 0.4rem;
    margin-bottom: 1.5rem;
}
@media screen and (max-width:600px){
    .register .sddd{
        font-size: 1rem;
    }
}

.register .inputbox{
    margin-bottom: 1rem;
    height: 2.4rem;
    width: 25rem;
}
@media screen and (max-width:600px){
    .register .inputbox{
      height: 1.4rem;
      width: 90%;
      padding: 0.3rem 0.3rem 0.3rem 0.3rem;
    }
}
.appbtn{
    margin-top: 0.9rem;
    font-size: 1.4rem;
    width: 100%;
    color: white;
    background-color: orangered;
    border-radius: 0.3rem;
}
@media screen and (max-width:600px){
    .appbtn{
        font-size: 0.7rem;
        width: 100%;
    }
}
.register .link{
    margin-top: 1rem;
}
@media screen and (max-width:600px){
    .register .link{
        font-size: 0.7rem;
    }
}