*{
    margin: 0;
    padding: 0;
}
article{
    width: 60%;
    margin-top: 6.4rem;
    margin-left: 12rem;
    
}
@media screen and (max-width:600px){
    article{
        margin-left: 0.2rem;
    }
}
.cart-heading{
   color: orangered;
   font-size: 2rem;
   font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
   margin-top: 0.5rem;
}
@media screen and (max-width:600px){
    .cart-heading{
        font-size: 1.6rem;
      font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
      margin-left: 0.2rem;
    }
}

.cart-box{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1rem;
    margin-left: auto;
    margin-right: auto;
    padding: 2%;
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem black;
    /* border-bottom: 0.1rem solid orangered ; */
    padding-bottom: 0.2rem;
}
@media screen and (max-width:600px){
    .cart-box{
        width: fit-content;
    }
}
.cart_img{
    display: flex;
    width: 25rem;
}
@media screen and (max-width:600px){
    .cart_img{
        width: fit-content;
    }
}
.cart_img img{
    width: 4.4rem;
    height: 3.4rem;
}
@media screen and (max-width:600px){
    .cart_img img{
        margin-left: -0.1rem;
        margin-top: 0.5rem;
        height: 1.8rem;
        width: 2.2rem;
    }
}
.cart_img p{
    font-size: 1.4rem;
    margin-left: 1.5rem;
    margin-top: 0.4rem;
}
@media screen and (max-width:600px){
    .cart_img p{
        font-size: 0.7rem;
        margin-top: 0.6rem;
        margin-left: -2.8rem;
    }
}

.cart-box div:nth-child(2) button{
    padding: 0.1rem 0.4rem 0.1rem 0.4rem;
    font-weight: bold;
    margin-right: 0.1rem;
    border-radius: 0.5rem;
    border: 0.2rem solid white;
    background-color: orangered;
    color: white;
}
@media screen and (max-width:600px){
    .cart-box div:nth-child(2) button{
        font-size: 0.4rem;
        font-weight: bold;
        border-radius: 0.3rem;
    }
}
.cart-box div:nth-child(3) button{
    padding: 0.1rem 0.4rem 0.1rem 0.4rem;
    margin-left: 0.2rem;
    margin-right: 0.4rem;
    font-weight: bold;
    letter-spacing: 0.1rem;
    outline: none;
    color: orangered;
    background-color: white;
    border-radius: 0.1rem;
    cursor: pointer;
}
@media screen and (max-width:600px){
    .cart-box div:nth-child(3) button{
        font-size: 0.7rem;
    }
}
.cart-box div:nth-child(3) button:hover{
    background-color: black;
    color: white;
}

.cart-box div:nth-child(3) span{
    padding: 0.1rem 0.2rem;
    border-radius: 0.1rem;
    margin-right: 1.2rem;
    font-weight: bold;
    font-size: 1.4rem;
}
@media screen and (max-width:600px){
    .cart-box div:nth-child(3) span{
        font-size: 0.8rem;
        border-radius: 0.3rem;
    }
}
.total{
    display: block;
    justify-content: space-between;
    padding: 3.5%;
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem black;
    width: fit-content;
}
.total #pt1{
    font-size: 1.5rem;
    color: black;
    font-weight: bold;
    margin-top: 1.4rem;
}
@media screen and (max-width:600px){
    .total #pt1{
        font-size: 0.7rem;
    }
}
.total #pt2{
    font-size: 1.3rem;
    color: orangered;
    font-weight: bold;
    margin-top: 1.4rem;
}
@media screen and (max-width:600px){
    .total #pt2{
        font-size: 0.7rem;
    }
}
.total #pt3{
    font-size: 1.3rem;
}
@media screen and (max-width:600px){
    .total #pt3{
        font-size: 0.7rem;
    }
}
.total #pt4{
    margin-top: 1rem;
    padding: 1%;
    color: white;
    width: 100%;
    background-color: orangered;
    border: 1px solid orangered;
}
@media screen and (max-width:600px){
    .total #pt4{
        font-size: 0.7rem;
    }
}

