.home{
    top: 5rem;
    width: fit-content;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    margin-top: 1rem;
}

@media screen and (max-width:900px){
    .home{
        flex-direction: column;
        margin-top: 1rem;
    }
}

.home1{
    margin-top: 4.5rem;
    /* margin-left: 5rem; */
}

@media screen and (max-width:900px){
    .home1{
        margin-left: 0.3rem;
    }
}

.home1 .sec1 {
    font-family: 'Times New Roman', Times, serif;
    font-size: 4.9rem;
    font-weight: 4rem;
    margin-top: 1rem;
    padding-bottom: -3rem;
    margin-left: 4.2rem;
}
.home1 .sec1 .foodies{
    color: orangered;
}

@media screen and (max-width:900px){
    .home1 .sec1{
        font-size: 1.4rem;
        margin-top: 1rem;
        margin-left: 1rem;
    }
}

.home1 .homepages{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
    margin-top: 1rem;
    margin-left: 4.2rem;
    font-size: 1.5rem;
}
@media screen and (max-width:900px){
    .home1 .homepages{
        margin-left: 1rem;
        font-size: 0.45rem;
    }
}
.home1 .btn{
    font-size: 1.2rem;
    color: white;
    background-color: orangered;
    margin-left: 10.8rem;
    margin-top: -3.9rem;
    box-shadow: 0.1rem 0.1rem 0.5rem 0.1rem orange;
    border: 0.1rem solid orangered;
    border-radius: 0.5rem;
    transition: 1s;
}

.home1 .btn:hover{
    transform: scale(1.1);
}

@media screen and (max-width:600px){
    .home1 .btn{
        font-size: 0.3rem;
        margin-left: 1.2rem;
        margin-top: -2rem;
        border-radius: 1.2rem;
    }
}

.home2 img{
    height: 30rem;
    width: 32.5rem;
    margin-top: 5.5rem;
    margin-left: 2.1rem;
    margin-right:  3.8rem;
    border-radius: 48% 52% 57% 43% / 34% 44% 56% 66%;
    animation-name: sample;
    animation-duration: 3s;
    animation-delay: 1s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}

@keyframes sample{
    0% {
        border-radius: 48% 52% 57% 43% / 34% 44% 56% 66%;
    }
    25% {
        border-radius: 62% 38% 64% 36% / 34% 55% 45% 66%;
    }
    50% {
        border-radius: 43% 57% 51% 49% / 33% 42% 58% 72%;
    }
    100% {
        border-radius: 40% 60% 43% 57% / 52% 32% 68% 48%;
    }
}


@media screen and (max-width:600px){
    .home2 img{
        margin-top: 1rem;
        margin-left: 0.4rem;
        height: 12rem;
        width: 12rem;
    }
}

