* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

 img{
    height: 21rem;
    width: 92%;
    margin-left: 3rem;
    margin-right: 3rem;
 }
 @media screen and (max-width: 600px){
    img{
        margin-left: 0.4rem;
        width: 50%;
    }
 }
.span1 .icon1{
    position: absolute;
    color: white;
    margin-top: -9rem;
    margin-left: 6.2rem;
}
@media screen and (max-width: 600px){
    .span1 .icon1{
        margin-left: 3rem;
    }
}
.span2{
    position: absolute;
    color: white;
    margin-top: -9rem;
    margin-left: 75rem;
}
@media screen and (max-width: 600px){
    .span2{
        color: white;
        margin-top: -9.3rem;
        margin-left: 10rem;
    }
}