.warning{
  height: auto;
  width: auto;
  position: fixed;
  right: 1%;
  top: 20%;
  background-color: red;
  color: white;
  font-family: cursive;
  border-radius: 0.4rem;
}

@media screen and (max-width: 600px) {
  .warning{
    position: fixed;
    font-size: 0.7rem;
    margin-left: 1rem;
    margin-right: 15rem;
  }
}

