.about{
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
}

@media screen and (max-width:600px){
    .about{
        flex-direction: column;
    }
}



.about .img img{
    height: 28rem;
    margin-top: 5.5rem;
}

@media screen and (max-width:600px){
    .img{
        width: auto;
    }
}

@media screen and (max-width:600px){
    .about .img img{
        height: 21rem;
    }
}



.about .srr{
    margin-top: 1.1rem;
    margin-right: 5rem;
}

@media screen and (max-width:600px){
    .srr{
        width: auto;
    }
}

.about .srr .abt{
    height: 3rem;
    font-size: 2rem;
    text-align: center;
    color: orangered;
    background-color: white;
    margin-top: 5.4rem;
    margin-left: 13rem;
    padding-top: 7px;
    padding-bottom: 5px;
    padding-left: 6px;
    padding-right: 5px;
    box-shadow: 2px 2px 2px 2px gray;
    border: 2px solid white;
    border-radius: 30px;
    transition: 1s;
}

@media screen and (max-width:600px){
    .about .srr .abt{
        font-size: 1.4rem;
        margin-left: 3.5rem;
    }
}

.about .srr .abt:hover{
    transform: scale(1.1);
}

.heading{
    margin-top: 2rem;
    margin-left: 10rem;
    margin-right: 0.7rem;
    font-size: 2rem;
}

@media screen and (max-width:600px){
    .heading{
        font-size: 1.6rem;
        margin-left: 1rem;
    }
}

p{
    color: orangered;
    margin-top: 5rem;
    margin-left: 4.5rem;
    margin-bottom: 1.4rem;
    font-size: 1.6rem;
}

@media screen and (max-width:600px){
    p{
        font-size: 1rem;
        margin-left: 1rem;
    }
}

.para{
    font-size: 17px;
    margin-left: 70px;
    padding-left: 4px;
    padding-right: 4px;
    color: black;
}
@media screen and (max-width:600px){
    .para{
        font-size: 0.5rem;
        margin-left: 0.8rem;
    }
}

.bnn{
    font-size: 17px;
    margin-left: 70px;
    padding-left: 4px;
    padding-right: 4px;
    color: white;
    background-color: orangered;
    border-radius: 8px;
    transition: 1s;
}
.bnn:hover{
    transform: scale(1.1);
}