.App {
    text-align: center;
  }
  
  .login{
    position: relative;
    top: 10rem;
    margin-left: 28rem;
    bottom: 5rem;
    height: fit-content;
    width: fit-content;
    background-color: rgba(255, 255, 255, 0.4) ;
    padding-top: 1rem;
    padding-left: 2rem;
    padding-bottom: 2rem;
    padding-right: 2rem;
    /* border: 0.4rem solid white; */
    border-radius: 0.2rem;
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem gray;
}

@media screen and (max-width:600px){
    .login{
      top: 6rem;
        margin-left: 0.2rem;
        margin-right: 0.1rem;
        height: 18rem;
        padding-left: 0.4rem;
        padding-right: 0.4rem;
        padding-bottom: 4rem;
        width: fit-content;
        background-color: rgba(255, 255, 255, 0.7) ;
    }
}

.head{
    font-size: 2rem;
    text-align: center;
    margin-top: 0.4rem;
    margin-bottom: 1.5rem;
}
@media screen and (max-width:600px){
  .head{
    font-size: 1.2rem;
  }
}


.input-group{
    margin-bottom: 0.1rem;
    height: 2.4rem;
    width: 25rem;
    border-radius: 0.3rem;
    border: 1px solid black;
    background-color: rgba(255, 255, 255, 0.1) ;
}
@media screen and (max-width:600px){
  .input-group{
    height: 1.4rem;
    width: 80%;
  }
}
.login .remember{
    font-size: 1.2rem;
}
@media screen and (max-width:600px){
  .login .remember{
    font-size: 0.8rem;
  }
}
.login-butn{
    margin-top: 0.5rem;
    font-size: 1.4rem;
    width: 100%;
    color: white;
    background-color: orangered;
    border-radius: 0.3rem;
}
@media screen and (max-width:600px){
  .login-butn{
    font-size: 0.7rem;
    width: 100%;
  }
}
.signup-link{
    font-size: 1.2rem;
    margin: 0.4rem;
}
@media screen and (max-width:600px){
  .signup-link{
    font-size: 0.6rem;
  }
}
