.container{
    font-family: 'Times New Roman', Times, serif;
    margin-left: 8rem;
    margin-right: 8rem;

}
@media screen and (max-width: 600px){
    .container{
        margin-left: 0.4rem;
}
}
.container .row .main-div{
    border: 1px solid gainsboro;
    border-radius: 0.1rem;
    background-color: white;
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem gray;
    width: fit-content;
    margin-top: 1rem;
    margin-left: 0.5rem;
    margin-right: 0.5rem;
    margin-bottom: 1rem;
    padding-bottom: 1rem;
}
.container .row .main-div:hover{
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem black;
}

.main-div .main-img img{
    height: 13.8rem;
    width: 15.7rem;
    border-radius: 50%;
    margin-top: 1.2rem;
    margin-left: 1.9rem;
    margin-right: 1.9rem;
}
@media screen and (max-width: 600px){
    .main-div .main-img img{
        height: 5.5rem;
        width: 6.5rem;
    }
}
.main-div .content{
    margin-top: 1rem;
}
.content h4{
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif ;
    margin-top: 0.6rem;
    text-align: center;
    background-color: white;
}

.main-bttt{
    margin-top: -1.5rem;
}
.main-div .main-bttt .cartbuttm{
    color: white;
    background-color: orangered;
    font-size: 1.1rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-radius: 0.2rem;
    padding: 0.2rem 0.2rem 0.2rem 0.2rem;
    width: 100%;
    border: 3px solid black;
 }
.main-bttt .cartbuttm:hover{
    color: white;
    background-color: black;
}

.main-div .main-bttt .cartbuttm .carts{
    margin-left: 0.5rem;
}



