*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

#contact{
    margin-top: 7.5rem;
    margin-left: 2rem;
}
@media screen and (max-width:900px) {
    #contact{
        margin-left: 5rem;
    }
}
#contact .container{
    display: flex;
    flex-direction: row;
}

@media screen and (max-width:900px){
    #contact .container{
        display: flex;
        flex-direction: column;
    }
}

.container .contactInfo{
    height:  24rem;
    width: 27%;
    border: 2px solid black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: whitesmoke;
    background: black;
    color: white;
    padding-top: 2rem;
    padding-left: 1.5rem;
}

@media screen and (max-width:900px){
    .container .contactInfo{
        height: 21rem;
        width: 65%;
        margin-top: -1rem;
        margin-left: -6rem;
        font-size: 1rem;
    }
    .container .contactInfo .contact{
        font-size: 1rem;
    }
    .container .contactInfo .ficon1{
        font-size: 0.1rem;
    }
}


.container .contactInfo .span1{
    margin-left: 1.2rem;
}
.container .contactInfo .contact{
    margin-bottom: 1rem;
}
.container .contactInfo .contact:hover{
    color: orangered;
}

.container .contactInfo span{
    font-size: 1rem;
}
.container .contactInfo .ficon1{
    font-size: 1.5rem;
    color: white;
}
.container .contactInfo .ficon1:hover{
    color: orangered;
}

.container .contactInfo .ficon{
    font-size: 1.6rem;
    margin-top: 4.8rem;
    margin-left: 0.8rem;
    color: white;
}
.container .contactInfo .ficon:hover{
    color: orangered;
}

@media screen and (max-width:900px){
    .container .contactInfo .ficon{
        margin-top: 1.5rem;
    }
}

.container .contactForm{
    height:  29.7rem;
    width: 68%;
    border: 2px solid black;
    margin-left: -5.8rem;
    margin-top: -2.8rem;
    padding-top: 5%;
    padding-left: 18%;
    padding-right: 2%;
    box-shadow: 0.1rem 0.1rem 0.1rem 0.1rem black;
}

@media screen and (max-width:900px){
    .container .contactForm{
        margin-top: 2rem;
        width: 55%;
        padding-left: 11%;
    }
}

.contactForm .h4{
    color: orangered;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 2rem;
    margin-top: -1.5rem;
    margin-bottom: 2rem;
}
@media screen and (max-width:900px){
    .contactForm .h4{
        font-size: 1.7rem;
        margin-top: 1rem;
    }
}

.contactForm .mesginput{
    height: 2.4rem;
    width: 85%;
    margin-bottom: 0.6rem;
}
.contactForm .mesgin1{
    height: 8.2rem;
    width: 85%;
    margin-bottom: 0.6rem;
}
.contactForm .contact-button4{
    width: 20%;
    margin-left: 0%;
    background-color: orangered;
    color: white;
    border: 2px solid white;
    padding-top: 0.5rem;
    padding-left: 0.6rem;
    padding-right: 0.6rem;
    padding-bottom: 0.5rem;
}
@media screen and (max-width:900px){
    .contactForm .contact-button4{
        height: 2.6rem;
        width: 4rem;
        padding-right: 0.4rem;
        color: white;
    }
}

