.main-container{
    margin-top: 4rem;
}
@media screen and (max-width: 600px){
    .main-container{
        margin-left: 1rem;
    }
}
.menutitle{
    margin-top: 5.5rem;
}

.menutitle .meenu{
    font-size: 1.6rem;
    text-align: center;
    color: orangered;
    margin-left: 29rem;
    margin-top: 15px;
    padding-top: 5px;
    padding-bottom: 5px;
    padding-left: 5px;
    padding-right: 5px;
    background-color: white;
    box-shadow: 2px 2px 2px 2px gray;
    border: 2px solid white;
    border-radius: 30px;
    transition: 1s;
}
@media screen and (max-width: 600px){
    .menutitle .meenu{
        font-size: 1rem;
        margin-left: 2.5rem;
        text-align: center;
        color: orangered;
    }
}
.menutitle .meenu:hover{
    transform: scale(1.3);
}
.menutitle .wake{
    margin-top: 20px;
    margin-bottom: 4px;
    color: black;
    font-family: 'Times New Roman', Times, serif;
    font-size: 50px;
    text-align: center;
}
@media screen and (max-width: 600px){
    .menutitle .wake{
        font-size: 1.4rem;
        margin-left: -9.5em;
    }
}

