.button_nav
{
    width: fit-content;
    display: flex;
    margin-left: 16.8rem;
    margin-right: 19.5rem;
    border: 2px solid white;
    box-shadow: 2px 2px 2px 2px gray;
    border-radius: 1.7rem;
    margin-bottom: 1rem;
    color: orangered;
    background-color: white;
    /* background-color: aqua; */
}
@media screen and (max-width: 600px){
    .button_nav{
        width: fit-content;
        display: flex;
        margin-left: -1.2em;
    }
}

.button_nav1
{
    width: fit-content;
    padding-left: 0.2rem; 
    padding-right: 0.2rem;  
}

.button_nav1 .buttom{
    color: orangered;
    background-color: white;
    border: 1px solid white;
    border-radius: 2rem;
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 1.5rem;
}
@media screen and (max-width: 600px){
    .button_nav1 .buttom{
        font-size: 10px; 
        padding-left: 1px;
        padding-right: 1px;  
    }
}

.button_nav1 .buttom:hover{
    color: white;
    background-color: orangered;
}