*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
 }
nav {
    background-color: black;
    position: fixed;
    font-family: "Poppins", sans-serif;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    top: 0;
    z-index: 9999;
    padding: 1em;
  }
  @media screen and (max-width: 600px){
    nav{
        width: max-content;
    }
  }
  @media screen and (max-width: 600px){
    .getbar{
      margin-left: 2.9rem;
    }
  }

  #logo i{
    color: whitesmoke;
    font-size: 2.2rem;
  }
  #logo .burgr{
    font-size: 2.2rem;
    color: orangered;
}
@media screen and (max-width: 600px){
  #logo i{
    font-size: 1rem;
  }
  #logo .burgr{
    font-size: 1rem;
  }
}
nav a {
    text-decoration: none;
    text-transform: capitalize;
    color: #ffffff;
  }
#logo {
    font-size: 1.2em;
    font-weight: 600;
}
input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
  }
label {
    display: none;
    cursor: pointer;
  }
  ul {
    list-style: none;
    display: flex;
    margin-top: 0.3rem;
    gap: 1.4em;
  }

  ul a {
    font-size: 1.4rem;
    padding: 0.3em;
  }
  ul a:hover {
    color: orangered;
    
  }
  .active {
    border-bottom: 2px solid #ffffff;
  }
  @media screen and (max-width: 600px) {
    label {
      display: block;
    }
    ul {
      font-size: 1.2em;
      position: absolute;
      left: -100%;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      background-color: #333333;
      gap: 2em;
      top: 60px;
      width: 100%;
      height: calc(100vh - 60px);
      transition: 0.5s;
    }
    input[type="checkbox"]:checked ~ ul {
      left: 0;
    }
    ul a:hover {
      background-color: #2c8eec;
    }
  }

  .shopcart{
    text-decoration: none;
    color: white;
    font-size: 1.4rem;
    margin-right: 0.4rem;
    border-radius: 0.3rem;
    padding-left: 0.4rem;
    padding-right: 0.4rem;
    padding-bottom: 0.3rem;
    background-color: rgb(31, 27, 27);
}

.shopcart .sup-part{
    color: orangered;
}

.userdet{
    text-decoration: none;
    color: white;
    font-size: 1.4rem;
    border-radius: 0.3rem;
    margin-left: 0.4rem;
    padding-left: 0.4rem;
    padding-right: 0.4rem;
    padding-bottom: 0.3rem;
    background-color: rgb(31, 27, 27); 
}

.cart{
    color: orangered;
    font-size: 1.2rem;
    margin-right: 0.2rem;
}

